@import "./bulma.scss";

html {
  background: #faf9fa;
}

body {
  background: #faf9fa;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial,
    sans-serif, "Apple Color Emoji", "Segoe UI Emoji", sans-serif;
  color: #222222;
  font-size: 16px;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  word-wrap: break-word;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

table td,
th {
  word-break: break-word;
}

table {
  width: 100%;

  tr {
    border-bottom: 1px solid rgba(0, 0, 0, 0.05);
  }

  td {
    padding: 8px 0;
  }
}

.ant-select-selector {
  border-radius: 16px !important;
}

.ant-collapse-header {
  padding: 0 !important;
  padding-bottom: 8px !important;
  padding-top: 8px !important;
  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
}

.network {
  font-size: 14px;
  font-weight: 600;
  background: #f3f3f4;
  color: #222222;
  height: 40px;
  padding: 0px 16px;
  box-shadow: none;
  border-radius: 16px;
  line-height: 40px;
  margin-right: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.label {
  color: #000000d9;
  font-size: 14px;
  font-weight: 600;
}

.noselect {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently supported by Chrome, Edge, Opera and Firefox */
}

.connectButton {
  border-radius: 16px;
  cursor: pointer;
  font-size: 16px;
  font-weight: 600;
  letter-spacing: 0.03em;
  line-height: 1;
  outline: 0px;
  height: 40px;
  padding: 0px 16px;
  background-color: #fdeaf1;
  color: #f95192;
  line-height: 40px;
  margin: 0 8px;
  display: flex;
  align-items: center;
  justify-content: center;

  &:last-child {
    margin: 0;
  }
}

.logo-icon {
  float: left;
  max-width: 36px;
}

.logo-text {
  float: left;
  font-size: 16px;
  font-weight: 600;
  margin-left: 10px;
}

@media all and (max-width: 1022px) {
  .hide-on-tablet {
    display: none;
  }
}

@media all and (max-width: 768px) {
  .hide-on-mobile {
    display: none;
  }
  .on-mobile {
    background-color: white;
    padding: 0 8px;
  }
}

.pool-filter {
  display: flex;
  align-items: center;
  margin-bottom: 32px;

  .invisible {
    opacity: 0;
  }

  .search {
    flex: 1;
  }

  .filter {
    display: flex;

    .status {
      margin-left: 16px;
      margin-right: 16px;
    }
  }

  .filter-title {
    font-size: 12px;
  }
}

@media all and (max-width: 520px) {
  .pool-filter {
    flex-direction: column;

    .invisible {
      display: none;
    }
  }

  .search {
    flex: 0;
    width: 100%;
  }

  .filter {
    width: 100%;
    margin-top: 8px;

    .status {
      margin-left: 0 !important;
      margin-right: 16px;
    }

    .ant-select {
      width: 100%;
    }

    & > div {
      flex: 1;
    }
  }
}

.flex {
  display: flex;
}

.items-center {
  align-items: center;
}

.justify-center {
  justify-content: center;
}

.flex-wrap {
  flex-wrap: wrap;
}

.flex-1 {
  flex: 1;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.2;
  font-weight: 600;
  margin: 0 0 1.5rem;
}

.web3modal-modal-lightbox {
  z-index: 31 !important;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}

input[type="range"] {
  width: 100%;
}

.input,
.textarea,
.select select {
  box-sizing: border-box;
  margin: 0;
  font-variant: tabular-nums;
  list-style: none;
  font-feature-settings: "tnum";
  position: relative;
  display: inline-block;
  width: 100%;
  min-width: 0;
  padding: 4.5px 11px;
  color: #000000d9;
  font-size: 14px;
  line-height: 1.5715;
  background-color: #fff;
  background-image: none;
  border: 1px solid #d9d9d9;
  border-radius: 4px;
  transition: all 0.3s;
  box-shadow: none;
  height: 32px;
}

.input:hover,
.textarea:hover,
.select select:hover {
  border-color: #ffd1c9;
}

.container {
  max-width: 1200px !important;
  padding: 0 1rem;
}

.custom-container {
  padding: 0 1rem;
}

.ant-layout {
  background: none;
}

.navbar {
  background: none !important;
}

.navbar-logo {
  display: flex;
  align-items: center;
}

a.logo {
  color: #333333;
}

a.logo:hover {
  color: #333333;
}

.navbar-menu a.navbar-item {
  padding: 0;
  margin: 0 0.75rem;
  color: #222222;
  text-decoration: none;
}

.navbar-menu a:hover.navbar-item {
  color: #222222;
  text-decoration: none;
  opacity: 0.85;
}

.navbar-item.last {
  padding: 0;
}

.hero-body {
  margin: 2.5rem 0 0;
  padding: 3rem 0 1.5rem;
}

.hero-body h2.custom-title {
  font-size: 60px;
  margin: 0 0 1.5rem;
}

.hero-body p.custom-subtitle {
  font-size: 20px;
  max-width: 70%;
  margin: 0 auto;
}

.custom-hero-buttons {
  width: 100%;
  margin: 0 auto;
  text-align: center;
}

.custom-hero-buttons a {
  align-items: center;
  border: 0px;
  border-radius: 16px;
  cursor: pointer;
  display: inline-flex;
  font-family: inherit;
  font-size: 16px;
  font-weight: 600;
  justify-content: center;
  letter-spacing: 0.03em;
  line-height: 1;
  opacity: 1;
  outline: 0px;
  height: 52px;
  padding: 0px 24px;
  background-color: #fdeaf1;
  color: #f95192;
