}

.custom-hero-buttons a.learn {
  margin-left: 20px;
}

.custom-hero-buttons a:hover {
  opacity: 0.85;
  text-decoration: none;
}

.stats {
  max-width: 90%;
  width: 90%;
  margin: 0 auto;
}

.stat {
  width: 25%;
  display: inline-block;
  padding: 0px 12px;
  margin: 0px 0px 24px;
  text-align: center;
}

.stat-box {
  max-width: 280px;
  margin: 0px auto;
  align-items: center;
  display: flex;
  justify-content: center;
  flex-direction: column;
  background: rgb(255, 255, 255);
  padding: 40px 32px;
  text-align: center;
  border-radius: 12px;
  box-shadow: rgba(0, 0, 0, 0.01) 0px 0px 1px, rgba(0, 0, 0, 0.01) 0px 4px 8px,
