}

.stat-box .title {
  font-size: 36px;
  font-weight: 600;
  margin-bottom: 5px;
}

.stat-box .heading {
  font-size: 15px;
  color: #666666;
  text-transform: none;
  letter-spacing: 0;
}

.features-section {
  margin: 0;
}

.features-section h3.custom-title {
  font-size: 48px;
  font-weight: 600;
  margin: 0 0 1.5rem;
  text-align: center;
}

.features-section p.custom-subtitle {
  font-size: 18px;
  max-width: 65%;
  margin: 1.5rem auto 3rem;
  text-align: center;
}

.feature-box {
  margin: 0px auto;
  align-items: center;
  display: flex;
  justify-content: center;
  flex-direction: column;
  background: #ffffff;
  padding: 2rem;
  border-radius: 16px;
  box-shadow: rgba(0, 0, 0, 0.01) 0px 0px 1px, rgba(0, 0, 0, 0.01) 0px 4px 8px,
    rgba(0, 0, 0, 0.01) 0px 8px 12px, rgba(0, 0, 0, 0.01) 0px 12px 16px;
}

.feature-box img {
  width: 84px;
  height: 84px;
}

.feature-box .custom-title {
  font-size: 26px;
  font-weight: 600;
  margin: 0.125rem 0 0.125rem;
}

.custom-features-section {
  padding: 2rem 0;
}

.custom-features-section h3.custom-title {
  font-size: 48px;
  margin-bottom: 1.5rem;
  text-align: center;
}

.custom-features-section p.custom-subtitle {
  font-size: 18px;
  max-width: 65%;
  margin: 0 auto 3rem;
  text-align: center;
}

.custom-features-section .custom-features {
  max-width: 94%;
  width: 94%;
  margin: 0 auto;
  text-align: center;
  display: flex;
  flex-wrap: wrap;
}

.custom-features-section .custom-features .custom-feature {
  display: inline-block;
  width: 25%;
  padding: 0px 12px;
  margin: 0px 0px 24px;
}

.custom-feature-box {
  max-width: 280px;
  margin: 0px auto;
  align-items: center;
  display: flex;
  justify-content: center;
  flex-direction: column;
  background: #ffffff;
  padding: 40px 32px;
  text-align: center;
  border-radius: 12px;
  height: 100%;
  box-shadow: rgba(0, 0, 0, 0.02) 0px 0px 1px, rgba(0, 0, 0, 0.02) 0px 4px 8px,
