}

.custom-feature-box img {
  max-width: 84px;
}

.custom-feature-box h3 {
  font-size: 28px;
  font-weight: 600;
  margin: 1.5rem 0px 0.5rem;
}

.custom-feature-box h3 a {
  color: #f95192;
  text-decoration: none;
}

.custom-feature-box h3 a:hover {
  color: #f95192;
  text-decoration: none;
  opacity: 0.85;
}

.custom-feature-box p {
  color: #888888;
}

.section-projects {
  margin: 3rem 0;
}

.section-projects h3.custom-title {
  font-size: 48px;
  margin-bottom: 1.5rem;
  text-align: center;
}

.section-projects p.custom-subtitle {
  font-size: 18px;
  max-width: 65%;
  margin: 0 auto 3rem;
  text-align: center;
}

.section-projects .projects {
  max-width: 90%;
  width: 90%;
  margin: 0 auto;
  text-align: center;
}

.section-projects .projects .project {
  display: inline-block;
  width: 25%;
  padding: 0px 12px;
  margin: 0px 0px 24px;
}

.project-box {
  max-width: 280px;
  margin: 0px auto;
  align-items: center;
  display: flex;
  justify-content: center;
  flex-direction: column;
  background: #ffffff;
  padding: 40px 32px;
  text-align: center;
  border-radius: 12px;
  box-shadow: rgba(0, 0, 0, 0.02) 0px 0px 1px, rgba(0, 0, 0, 0.02) 0px 4px 8px,
    rgba(0, 0, 0, 0.02) 0px 16px 24px, rgba(0, 0, 0, 0.02) 0px 24px 32px;

  p {
    word-break: normal;
  }
}

.project-box img {
  max-width: 84px;
}

.project-box h3 {
  font-size: 28px;
  font-weight: 600;
  margin: 1.5rem 0px 0.5rem;
}

.project-box h3 a {
  color: #f95192;
  text-decoration: none;
}

.project-box h3 a:hover {
  color: #f95192;
  text-decoration: none;
  opacity: 0.85;
}

.ant-layout-footer {
  padding: 0;
  background: none;
  font-size: 15px;
  color: #222222;
}

.footer {
  padding: 1rem 0 3rem;
}

.copyright {
  font-size: 15px;
  max-width: 75%;
  margin: 0px auto 4rem;
  text-align: center;
}

.navbar-item .button.is-primary {
  border: 0px;
  border-radius: 16px;
  cursor: pointer;
  font-size: 16px;
  font-weight: 600;
  letter-spacing: 0.03em;
  line-height: 1;
  opacity: 1;
  outline: 0px;
  height: 40px;
  padding: 0px 16px;
  background-color: #fdeaf1;
  box-shadow: none;
  color: #f95192;
  box-shadow: 0 2px 0 rgba(0, 0, 0, 0.02);
}

.navbar-item .button.is-primary:hover,
.navbar-item .button.is-primary:focus,
.navbar-item .button.is-primary.is-focused,
.navbar-item .button.is-primary:focus:not(:active),
.navbar-item .button.is-primary.is-focused:not(:active) {
  font-size: 16px;
  font-weight: 600;
  border: 0px;
  background-color: #fdeaf1;
  box-shadow: none;
  color: #f95192;
  opacity: 0.85;
  outline: none;
}

.navbar-item .buttons .button,
.navbar-item .buttons:last-child {
  margin-bottom: 0;
}

.navbar-button {
  font-size: 16px;
  font-weight: 600;
  letter-spacing: 0.03em;
  line-height: 1;
  background: #f3f3f4;
  color: #222222;
  height: 40px;
  padding: 0px 16px;
  opacity: 1;
  box-shadow: none;
  border-radius: 16px;
  border: none;
}

.navbar-button:hover {
  opacity: 0.85;
  text-decoration: none;
}

.custom-card {
  border-radius: 32px;
  box-shadow: rgba(25, 19, 38, 0.1) 0px 2px 12px -8px,
    rgba(25, 19, 38, 0.05) 0px 1px 1px;
}

.custom-card .image img {
  width: 48px;
  height: 48px;
  border-radius: 50%;
}

.custom-card .status-dot {
  background-color: #ffeaef;
  color: #ff3465;
  font-size: 12px;
  line-height: 18px;
  font-weight: 600;
  display: inline-flex;
  align-items: center;
  vertical-align: middle;
  padding: 4px 8px;
  border-radius: 50px;
}

.custom-card .status-dot.ended {
  background-color: #ffeaef;
  color: #ff3465;
}

.custom-card .status-dot.inprogress {
  background-color: #d1fae5;
  color: #10b981;
}

.custom-card .status-dot.incoming {
  background-color: #fdfaea;
  color: #d29813;
}

.custom-card .status-dot.canceled {
  background-color: #e9eaeb;
  color: #bdc2c4;
}

.custom-card .status-dot svg {
  font-size: 8px;
  margin-right: 4px;
}

.custom-card .media:not(:last-child) {
  margin-bottom: 1rem;
}

.custom-card .content-title {
  margin: 0 0 1rem;
}

.custom-card .content-title .title {
  font-size: 22px;
  font-weight: 600;
  margin: 0;
}

.custom-card .content-title .subtitle {
  font-size: 15px;
  font-weight: 600;
  color: #888888;
  margin: 0 !important;
}

.custom-card .soft-hard-cap {
  line-height: 1.125;
  margin: 0 0 0.5rem;
}

.custom-card .soft-hard-cap p {
  font-size: 15px;
  font-weight: 600;
  color: #222222;
  margin: 0 0 0.25rem;
}

.custom-card .soft-hard-cap span {
  font-size: 20px;
  font-weight: 600;
  color: #f95192;
  margin: 0;
}

.custom-card .content-progress {
  line-height: 1.125;
  margin: 0 0 1rem;
}

.custom-card .content-progress p.title {
  font-size: 15px;
  font-weight: 600;
  color: #222222;
  margin: 0 0 0.25rem;
}

.custom-card .content-progress progress {
  height: 0.5rem;
}

.custom-card .content-progress .sub-title {
  font-size: 13px;
  font-weight: 600;
  color: #888888;
}

.custom-card .start-time-content {
  margin: 0;
}

.custom-card .start-time .time-text {
  font-size: 14px;
}

.custom-card .end-time-content {
  margin: 0;
}

.custom-card .end-time .time-text {
  font-size: 14px;
}

.custom-card .liquidity-percent {
  line-height: 1.125;
  margin: 0 0 0.25rem;
}

.custom-card .liquidity-percent p {
  font-size: 15px;
  font-weight: 600;
  color: #222222;
  margin: 0;
}

.custom-card .liquidity-percent span {
  font-size: 14px;
  font-weight: 600;
  color: #888888;
}

.custom-card .lockup-time {
  line-height: 1.125;
  margin: 0 0 1.5rem;
}

.custom-card .lockup-time p {
  font-size: 15px;
  font-weight: 600;
  color: #222222;
  margin: 0;
}

.custom-card .lockup-time span {
  font-size: 14px;
  font-weight: 600;
  color: #888888;
}

.custom-card .custom-card-footer {
  padding: 1.5rem 0 0;
  margin: 0;
  border-top: 1px solid #e9eaeb;
}

.custom-card .custom-card-footer .countdown {
  font-size: 14px;
  line-height: 1.3;
  color: #222222;
}

.custom-card .custom-card-footer .countdown .dotdot {
  margin-right: 1px;
}

.custom-card .custom-card-footer .view-pool a {
  display: inline-flex;
  align-items: center;
  font-size: 15px;
  font-weight: 600;
  text-decoration: none;
  border: none;
  border-radius: 16px;
  cursor: pointer;
  letter-spacing: 0.03em;
  line-height: 1;
  opacity: 1;
  outline: 0px;
  height: 40px;
  padding: 0px 16px;
  background-color: #fdeaf1;
  box-shadow: none;
  color: #f95192;
}

.custom-card .custom-card-footer .view-pool a:hover {
  opacity: 0.85;
  background-color: #fdeaf1;
  box-shadow: none;
  color: #f95192;
}

.custom-card .card-button {
  padding: 1.5rem 0 0;
  margin: 0;
  text-align: center;
  border-top: 1px solid #e9eaeb;
}

.custom-card .card-button .view-button {
  font-size: 16px;
  font-weight: 600;
  color: #f95192;
  text-decoration: none;
}

.custom-card .card-button .view-button:hover {
  color: #f95192;
  text-decoration: underline;
}

.custom-card .card-button .button {
  -webkit-box-align: center;
  align-items: center;
  border: 0px;
  border-radius: 16px;
  cursor: pointer;
  display: inline-flex;
  font-family: inherit;
  font-size: 16px;
  font-weight: 600;
  -webkit-box-pack: center;
  justify-content: center;
  letter-spacing: 0.03em;
  line-height: 1;
  opacity: 1;
  outline: 0px;
  height: 50px;
  padding: 0px 24px;
  background-color: rgb(253, 234, 241);
  color: rgb(249, 81, 146);
  width: 100%;
  margin: 0 auto;
}
.custom-card .card-button .button:hover,
.custom-card .card-button .button:focus,
.custom-card .card-button .button.is-focused,
.custom-card .card-button .button:focus:not(:active),
