.loader,
.loader:after {
  border-radius: 50%;
  width: 28px;
  height: 28px;
}
.loader {
  margin: 0 auto;
  font-size: 10px;
  position: relative;
  text-indent: -9999em;
  border-top: 3px solid rgba(0, 0, 0, 0.05);
  border-right: 3px solid rgba(0, 0, 0, 0.05);
  border-bottom: 3px solid rgba(0, 0, 0, 0.05);
  border-left: 3px solid rgb(241, 107, 107);
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation: load8 1.1s infinite linear;
  animation: load8 1.1s infinite linear;
}
@-webkit-keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

.provider-item {
  padding: 8px 12px;
  font-weight: 600;
  cursor: pointer;
  display: flex;
  align-items: center;
  margin-bottom: 8px;
  background-color: #fdeaf1;
  color: #f95192;
  border: 1px solid #f9519128;
  border-radius: 4px;
  transition: all 250ms ease;

  &:hover {
    border: 1px solid #f95192;
  }

  .icon {
    width: 28px;
    height: 28px;
  }

  .title {
    font-size: 14px;
    color: #f95192;
    flex: 1;
    margin: 0;
  }
}

.ant-menu-title-content {
  font-size: 16px;
}

.flex-wrapper {
  display: flex;
  flex-flow: row nowrap;
}

.single-chart {
  width: 60px;
  justify-content: space-around ;
}

.circular-chart {
  display: block;
  margin: 10px auto;
  max-width: 80%;
  max-height: 250px;
}

.circle-bg {
  fill: none;
  stroke: #eee;
  stroke-width: 3.8;
}

.circle {
  fill: none;
  stroke-width: 2.8;
  stroke-linecap: round;
  animation: progress 1s ease-out forwards;
}

@keyframes progress {
  0% {
    stroke-dasharray: 0 100;
  }
}

.circular-chart.orange .circle {
  stroke: #ff9f00;
}

.circular-chart.green .circle {
  stroke: #4CC790;
}

.circular-chart.blue .circle {
  stroke: #3c9ee5;
}

.percentage {
  fill: #666;
  font-family: sans-serif;
  font-size: 0.5em;
  text-anchor: middle;
}

.collapse-content {
  .ant-collapse-content-box {
    padding: 0 !important;
    margin-left: -20px;
    margin-right: -20px;
  }

  .ant-collapse-header {
    font-size: 12px;
    border: none;
  }
}
