.trending {
  background-color: white;
  margin-top: 31px;
  margin-left: -20px;
  margin-right: -20px;
  display: flex;
  align-items: center;
}

.title {
  padding: 10px;
  font-weight: 600;
  color: rgba(0, 0, 0, 0.87);
}

.title svg {
  color: #f95192;
}

.items {
  flex: 1;
  /* text-align: right; */
  list-style: none;
}

.trendingItem {
  display: inline-block;
  padding: 10px 8px;
  font-size: 13px;
}

.trendingItem:first-child {
  font-weight: 500;
}